import React, { useEffect } from "react";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";
import Select from "../components/FormElements/Select";

const CreateCoupon = () => {
  useEffect(() => {
    changeTitle("Create New Coupon");
  }, []);

  return (
    <div className="basic-form">
      <form>
        <FormGroup label="Coupen Code">
          <Input type="name" placeholder="Coupon Code" />
        </FormGroup>

        <FormGroup label="Coupen Code">
          <Select
            options={[
              {
                text: "Percentage",
                value: "Percentage",
              },
              { text: "Amount", value: "amount" },
            ]}
          ></Select>
        </FormGroup>

        <FormGroup label="Amount / %age">
          <Input type="number" placeholder="Amount / percentage" />
        </FormGroup>

        <FormGroup>
          <FormButton>Add Coupon</FormButton>
        </FormGroup>
      </form>
    </div>
  );
};

export default CreateCoupon;
