import React, { useEffect } from "react";

import changeTitle from "../utils/change-title";
import FormButton from "../components/Buttons/FormButton";
import FormGroup from "../components/FormElements/FormGroup";
import Input from "../components/FormElements/Input";

const CreateEventItem = () => {
  useEffect(() => {
    changeTitle("Create New Event Item");
  }, []);

  return (
    <div className="basic-form">
      <form>
        <FormGroup label="Event Item Name *">
          <Input type="name" placeholder="Event Item Name" />
        </FormGroup>

        <FormGroup label="Event Item Price *">
          <Input type="number" placeholder="Event Item Price" />
        </FormGroup>
        <FormGroup>
          <FormButton>Add Event Item</FormButton>
        </FormGroup>
      </form>
    </div>
  );
};

export default CreateEventItem;
